.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;

}


.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;

}

.showElement{
  animation: showElement 2s  both;
}

@keyframes showElement {
  from{
    transform: translateY(423px);
  }

  to{
    transform: translateY(0px);
  }
}

.App-link {
  color: #61dafb;
}

button{
  background-color: #61dafb;
  padding:12px;
  border-radius: 23px;
  font-weight: bolder;
  color: black;
  width: 304px;
  border: none;
  cursor: pointer;
  margin-top: 23px;
  

}

.input{
  height: 23px;
  width: 304px;
  background-color: transparent;
  border: solid 2px #61dafb;
  border-radius: 23px;
  outline: #61dafb;
  padding: 10px;
  color: white;
  

}
.clr{
  color: #61dafb;
  margin-right: 20px;
}
.rightButton{
  float: right;
}

.inputNot{
  border:  solid red 2px;
}

.inputNot::placeholder{
  color: red;
}
.inter{
  margin-top: -23px;
  text-align: left;
  padding: 20px;
}


h2{
  animation: typing 4s linear;
}


@keyframes typing {
  from{
    overflow: hidden;
    width: 10%;
  }

  to{
    overflow: hidden;
    width: 100%;
  }
}

span{
  background-color: #282c34;
  color: #61dafb;
  font-weight: bolder;
  position: fixed;
  text-align: center;
  bottom: 10px;
  width: 100vw;

}


.inter input{
  width: 100%;
  padding: 11px;
  border-radius: 23px;
  background-color: #61dafb;
  border: none;
  cursor: pointer;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


@media screen and (max-width:900px) {

  .input,button{
    width: 80vw;
  }
  
}

